header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.actn {
    margin-top:2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* Portrait */
.me {
background: linear-gradient(var(--color-primary), transparent);
width: 22rem;
height: 30rem;
position: absolute;
left: calc(50% - 11rem);
margin-top: 4rem;
margin-bottom: 4rem;
border-radius: 12rem 12rem 0 0;
overflow: hidden;
padding: 5rem 1.5rem 1.5rem 1.5rem;

}


/* Tablets */
@media  screen and (max-width: 1024px) {
 header{
    height: 100vh;
 }
}

/* Phones */
@media  screen and (max-width: 600px) {
    header{
       height: 100vh;
    }
    .scroll__down {
        display: none;
    }
   }